.App {
    padding: 2rem;
    display: grid;
    grid-template: 100px 200px 1fr / 1fr 600px 300px 1fr;
    grid-template-areas: 
    "title title title title"
    ". header header ."
    ". main info .";
}

.title {
    grid-area: title;
    display: flex;
    justify-content: center;
}



.header {
    grid-area: header;
    margin-bottom: -1px;
}

.main {
    grid-area: main;
}

.info {
    grid-area: info;
    margin-left: -1px;
}

.header, .main, .info {
    padding: 2rem;
    border: solid 1px;
}

input, textarea {
    border: none;
}

textarea {
    resize: none;
}